.Login{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  &-content{
    width: 638px;
    height: 745px;
    background: #FFFFFF;
    box-shadow: 0px 0px 30px 0px rgba(7, 20, 55, 0.17);
    border-radius: 8px;
    margin-top: 160px;
    margin-left: 1082px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &-icon{
    width: 420px;
    height: 100px;
    margin-top: 70px;
    background-size: cover;
  }
  &-title{
    font-size: 54px;
    font-family: SourceHanSansCN-Heavy, SourceHanSansCN;
    font-weight: 800;
    color: #222222;
    line-height: 73px;
  }
  &-form{
    width: 420px;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &-input{
    width: 420px;
    height: 60px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    border: 1px solid #0075C2;
  }
  &-login{
    width: 422px;
    height: 60px;
    background: #004C9E;
    border-radius: 4px;
    font-size: 24px;
    font-family: SourceHanSansSC-Bold, SourceHanSansSC;
    font-weight: bold;
    color: #FFFFFF;
    line-height: 36px;
    margin-top: 40px;
  }
}

@primary-color: #1890ff;