  text-align: left;
}
.tacenter {
  text-align: center;
}
.taright {
  text-align: right;
}
/*
 * 布局
 */
.fr {
  float: right;
}
.fl {
  float: left;
}
.dil {
  display: inline-block;
}
.overh {
  overflow: hidden;
}
.flexcenter {
  display: flex;
  align-items: center;
}
/* 大小 */
.width1 {
  width: 100%;
}
.width100 {
  width: 100%;
}
.height1 {
  height: 100%;
}
/* 样式 */
.cursorp {
  cursor: pointer;
}
.cursor {
  cursor: pointer;
}
.cursor:hover {
  opacity: 0.75;
}
.cursort {
  cursor: text;
}
.bgWhite > td {
  background: #fff !important;
}
.ftTransparent {
  background: transparent;
  color: transparent;
}
.notallowed {
  color: rgba(0, 0, 0, 0.45);
  cursor: not-allowed;
}
.notallowed .anticon {
  opacity: 0.6;
}
/* 字体 */
.antfont {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}
.stepbg {
  height: 16px;
  width: 16px;
  margin-right: 3px;
  text-align: center;
  line-height: 16px;
  background: #e0e7f5;
  border-radius: 50%;
}
/* 输入框正常状态以及报错状态 (适用于双联判断通过 className 来校验得情况) */
.inputBorderNormal {
  border: 1px solid #d9d9d9;
}
.ellipsisStyle {
  word-wrap: break-word;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.flexaligncenter {
  display: flex;
  align-items: center;
}
/* flex 布局，两端对齐 */
.between_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.disabled {
  cursor: not-allowed;
  color: rgba(0, 0, 0, 0.25);
}
