.BiciHeader{
  width: 300px;
  height: 500px;
  position: relative;
  overflow: auto;
  &-ellipsis{
    position: absolute;
    font-size: 20px;
    right: 10px;
    top: 10px;
    cursor: pointer;
  }
  &-message{
    width: 100%;
    min-height: 50px;
    max-height: 100px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f0f0f0;
    &Content{
      padding-left: 10px;
      display: flex;
      flex-direction: column;
      color: rgba(0,0,0,0.6);
      &Font{
        width: 200px;
        max-height: 50px;
        overflow: hidden;
        text-overflow: ellipsis;
        flex-wrap: nowrap;
      }
    }
    &Content:hover{
      cursor: pointer;
      color: #000;
    }
  }
  &-circle{
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background: red;
  }
  &-msg{
    display: flex;
    align-items: center;
    cursor: pointer;
  }
}

@primary-color: #1890ff;